import React, { Component } from 'react'
import style from './info.module.css';

export class Info extends Component {
    render() {
        return (
            <div className={style.info}>
                <button>
                    &#8520;
                    <span className={style.tooltiptext}>
                        <p>Info:</p>
                        <p>Input any dish you want</p>
                        <p>and if you want to find</p>
                        <p>an easy dish with maximum</p>
                        <p>of 5 ingredients just</p>
                        <p>click easy tag.</p>
                    </span>
                </button>
            </div>
        )
    }
}

export default Info
