import React from 'react';
import style from './result.module.css';

const Result = ({ title, calories, image, source, url, ingredients }) => {
	return (
		<div className={style.result}>
			<h1 className={style.title}>{title}</h1>
			<img className={style.image} src={image} alt="" />
			<span className={style.calories}>
				<b>Calories: </b>
				{numberWithCommas(calories.toFixed(2))}
			</span>
			<ul className={style.ingredients}>
				<b>Ingredients:</b>
				{ingredients.map(ingredients => (
					<li>{ingredients.text}</li>
				))}
			</ul>
			<a href={url}>
				<span className={style.sourceLabel}>See full recipe on: </span>
				<span className={style.sourceLink}>{source}</span>
			</a>
		</div>
	);
}

function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default Result;