import React, { useEffect, useState } from 'react';
import RecipeResult from './RecipeResult';
import NoResult from './NoResult';
import Info from './Info';
import './App.css';

const App = () => {

    const APP_ID = '244ea43b';
    const APP_KEY = '07e940acdb8f63eef87c8f248c71fb40';

    const [recipes, setRecipes] = useState(false);
    const [search, setSearch] = useState('');
    const [query, setQuery] = useState('');
    const [maxIngredient, setMaxIngredient] = useState(100);
    
    const getRecipes = async () => {
        if (query) {
            const response = await fetch(
                `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=20&ingr=${maxIngredient}`
            );
    
            const data = await response.json();

            if (data.hits && data.hits.length) {
                setRecipes(data.hits);
            } else {
                setRecipes(false);
            }
        } 
    };

    const updateSearch = e => {
        setSearch(e.target.value);
    }

    const getSearch = e => {
        e.preventDefault();
        setQuery(search);
        setSearch('');
    }

    const getMaxIngredient = e => {
        if (e.target.checked) {
            setMaxIngredient(5);
        } else {
            setMaxIngredient(100);
        }
    }
    
    useEffect(() => {
        getRecipes();
        setQuery('');
    }, [query]);

    return (
        <div className="App">
            <Info />
            <form onSubmit={getSearch} className="search-form">
                <span className="search-tag">
                    <input type="checkbox" id="checkboxOne" onChange={getMaxIngredient} />
                    <label for="checkboxOne">EASY</label>
                </span>
                <input className="search-bar" type="text" value={search} onChange={updateSearch} placeholder="Enter any dish" />
                <button className="search-button" type="submit">Search</button>
            </form>

            { recipes ? <RecipeResult recipes={recipes}/>: <NoResult /> }
            
        </div>
    );
};

export default App;